.ruler-controls {
  display: flex;
  gap: 1px;
  align-items: center;
  margin-left: 5px;
  padding: 2px;
  border: 1px solid #444;
  border-radius: 8px;
  background-color: #2b2b2b;
}

.ruler-controls select,
.ruler-controls button {
  padding: 5px 10px;
  font-size: 14px;
  width: 80px;
  background-color: #222;
  color: #fff;
  border: 1px solid #555;
  border-radius: 4px;
  cursor: pointer;
}

.ruler-controls button:hover {
  background-color: #444;
}
