.left-bar {
  display: flex;
  flex-direction: column;
  background-color: #2b2b2b; /* Background color for the left bar */
  padding: 8px;
}

.icon-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 15px; /* Space between the top and the first icon */
  gap: 18px;
}

.dropdown-container {
  position: relative;
  align-items: center;
}

.dropdown-trigger {
  cursor: pointer;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px;
  border: 1px solid #444;
  border-radius: 4px;
  background-color: #333; /* Keep dropdown trigger background */
}

.dropdown-menu {
  position: absolute;
  top: 100%; /* Position below the trigger */
  left: 0;
  background-color: #333; /* Keep dropdown menu background */
  border: 1px solid #444;
  border-radius: 4px;
  padding: 5px;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.dropdown-item {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 5px;
  cursor: pointer;
  color: #fff; /* Keep dropdown items white */
  border-radius: 4px;
}

.dropdown-item:hover {
  background-color: #444; /* Hover effect for dropdown items */
}

.dropdown-item.selected {
  background-color: #555; /* Selected item background */
  font-weight: bold;
}

/* Ensure all icons below the dropdown are white */
.icon {
  color: #fff; /* Set all icons below the dropdown to white */
  cursor: pointer;
  transition: transform 0.2s ease, color 0.2s ease;
}

.icon:hover {
  transform: scale(1.1); /* Slight zoom effect on hover */
  color: #00d8ff; /* Optional hover color */
}

  .bottom-icon {
    margin-top: auto; /* Push the settings icon to the bottom */
    display: flex;
    justify-content: center; /* Center the icon horizontally */
    padding-bottom: 10px; /* Add some spacing at the bottom */
  }