.settings-popup {
    position: fixed;
    top: 80px;
    left: 80px;
    background-color: #1e1e1e;
    color: #f0f0f0;
    padding: 20px;
    width: 300px;
    border-radius: 12px;
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.4);
    z-index: 1000;
    font-family: 'Segoe UI', sans-serif;
    border: 1px solid #444;
  }
  
  .settings-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
  }
  
  .settings-header h3 {
    font-size: 18px;
    margin: 0;
  }
  
  .close-btn {
    background: transparent;
    border: none;
    font-size: 22px;
    color: #ccc;
    cursor: pointer;
    transition: color 0.2s ease;
  }
  
  .close-btn:hover {
    color: red;
  }
  
  .settings-tabs {
    display: flex;
    gap: 8px;
    margin-bottom: 15px;
  }
  
  .settings-tabs button {
    flex: 1;
    background-color: #2b2b2b;
    color: #ccc;
    padding: 6px 10px;
    border: 1px solid #444;
    border-radius: 6px;
    font-size: 14px;
    cursor: pointer;
    transition: background 0.2s ease;
  }
  
  .settings-tabs button.active {
    background-color: #007bff;
    color: white;
    border-color: #005bb5;
  }
  
  .settings-tabs button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
  
  .settings-content {
    padding-top: 5px;
  }
  
  .grid-settings {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .grid-settings label {
    font-size: 14px;
    margin-bottom: 4px;
    color: #ccc;
  }
  
  .grid-settings input[type='range'] {
    width: 100%;
  }
  
  .grid-settings span {
    font-size: 13px;
    text-align: right;
    color: #aaa;
  }
  .toggle-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
    font-size: 14px;
    color: #ccc;
  }
  
  .switch {
    position: relative;
    display: inline-block;
    width: 42px;
    height: 22px;
  }
  
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #666;
    transition: 0.4s;
    border-radius: 30px;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 16px;
    width: 16px;
    left: 4px;
    bottom: 3px;
    background-color: white;
    transition: 0.4s;
    border-radius: 50%;
  }
  
  input:checked + .slider {
    background-color: #2196F3;
  }
  
  input:checked + .slider:before {
    transform: translateX(18px);
  }

  .setting-row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 15px;
  }
  
  .setting-row label {
    font-size: 14px;
    font-weight: 500;
  }
  
  .setting-row input[type="color"] {
    width: 40px;
    height: 30px;
    border: none;
    background: none;
  }
  
  .setting-row select {
    width: 150px;
    padding: 5px;
    font-size: 14px;
  }
  
  .slider-row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
  }
  .toggle-gridtype {
    display: flex;
    align-items: center;
    gap: 15px;
  }
  
  .radio-group {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  
  .radio-group input[type="radio"] {
    margin-right: 4px;
  }
  
  
  